:root {
  --app-header-height: 60px;
  --sidebar-width: 16%;
  --response-title-height: auto;
  --chat-input-area-min-height: 40px;
}

body {
  color: #333;
  background-color: #f6f8fc;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
}

.app-shell {
  grid-template-rows: var(--app-header-height) 1fr;
  grid-template-areas: "appheader"
                       "appbody";
  width: 100vw;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

.app-header {
  box-sizing: border-box;
  grid-area: appheader;
  justify-content: space-between;
  align-items: center;
  padding: 0 25px;
  display: flex;
}

.app-logo-link {
  text-decoration: none;
}

.app-logo-link.hidden-on-main {
  visibility: hidden;
}

.app-logo {
  color: #24292e;
  margin: 0;
  font-size: 1.8em;
  font-weight: 600;
}

.app-logo .ai-red {
  color: #cb2431;
}

#setting-button {
  cursor: pointer;
  background: none;
  border: none;
  width: 28px;
  height: 28px;
  padding: 0;
}

#setting-button img {
  width: 100%;
  height: 100%;
  display: block;
}

.app-body-container {
  grid-area: appbody;
  grid-template-columns: var(--sidebar-width) 1fr var(--sidebar-width);
  background-color: #f6f8fc;
  grid-template-areas: "leftsidebar maincontent rightsidebar";
  gap: 0;
  display: grid;
  overflow: hidden;
}

.sidebar {
  padding: 20px;
  overflow-y: auto;
}

.left-sidebar {
  grid-area: leftsidebar;
}

.right-sidebar {
  grid-area: rightsidebar;
}

.left-sidebar h2, .right-sidebar h2 {
  color: #586069;
  border-bottom: 1px solid #e1e4e8;
  margin-top: 0;
  padding-bottom: 8px;
  font-size: 1.1em;
}

#history-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

#history-list li a {
  color: #0366d6;
  white-space: nowrap;
  text-overflow: ellipsis;
  padding: 8px 0;
  font-size: .9em;
  text-decoration: none;
  display: block;
  overflow: hidden;
}

#history-list li a:hover {
  background-color: #c8c8c81a;
  text-decoration: underline;
}

.main-content-column {
  background-color: #fff;
  border-radius: 15px;
  flex-direction: column;
  grid-area: maincontent;
  min-width: 0;
  margin-bottom: 5px;
  padding: 20px 20px 5px;
  display: flex;
  overflow: hidden;
}

.page-section {
  box-sizing: border-box;
  flex-grow: 1;
  width: 100%;
  display: none;
}

.page-section.active {
  flex-direction: column;
  display: flex;
}

#url-input-section.active {
  text-align: center;
  justify-content: center;
  align-items: center;
}

.url-input-title {
  text-align: center;
  color: #24292e;
  margin-top: -20vh;
  margin-bottom: 40px;
  font-size: 3.2em;
  font-weight: 600;
}

.url-input-title .ai-red {
  color: #cb2431;
}

.input-form-container {
  width: 80%;
  max-width: 700px;
  position: relative;
}

.url-textarea {
  box-sizing: border-box;
  resize: none;
  border: 1px solid #d1d5da;
  border-radius: 30px;
  width: 100%;
  min-height: calc(3em + 24px);
  max-height: 12em;
  padding: 12px 60px 12px 30px;
  font-size: 1em;
  line-height: 1.5;
  transition: border-radius .2s ease-in-out, box-shadow .2s ease-in-out;
  overflow-y: auto;
}

.url-textarea:focus {
  border-color: #0366d6;
  outline: none;
  box-shadow: 0 0 0 3px #0366d64d;
}

.custom-submit-button {
  color: #fff;
  cursor: pointer;
  user-select: none;
  background-color: #0366d6;
  border: 2px solid #0366d6;
  border-radius: 20px;
  align-items: center;
  min-width: 100px;
  height: 40px;
  padding: 0;
  font-size: .9em;
  transition: background-color .2s;
  display: inline-flex;
  overflow: hidden;
}

.custom-submit-button:hover {
  background-color: #005cc5;
}

.custom-submit-button .submit-text {
  padding: 0 5px 0 15px;
  font-weight: 500;
  line-height: 40px;
}

.custom-submit-button .shortcut-key {
  color: #fffc;
  padding: 0 15px 0 0;
  font-size: .85em;
  line-height: 40px;
}

#url-input-section .custom-submit-button {
  position: absolute;
  bottom: 15px;
  right: 7px;
}

#response-section.active {
  overflow: hidden;
}

.response-content-wrapper {
  flex-grow: 1;
  min-height: 0;
  padding-bottom: 10px;
  overflow-y: auto;
}

#response-section h2 {
  color: #24292e;
  border-bottom: 1px solid #e1e4e8;
  flex-shrink: 0;
  margin: 0 0 20px;
  padding-bottom: 10px;
  font-size: 1.6em;
}

.fixed-bottom-input {
  min-height: var(--chat-input-area-min-height);
  flex-shrink: 0;
  align-items: flex-end;
  padding: 6px 0;
  display: flex;
  position: relative;
}

#additional-question-input.url-textarea {
  border-radius: 22px;
  flex-grow: 1;
  min-height: calc(3em + 22px);
  margin-left: 10px;
  margin-right: 10px;
  padding-right: 50px;
}

#response-section .custom-submit-button {
  margin-left: auto;
  position: absolute;
  bottom: 19px;
  right: 28px;
}

#response-output {
  max-width: 100%;
}

#response-output h1, #response-output h2, #response-output h3, #response-output h4, #response-output h5, #response-output h6 {
  color: #24292e;
  border-bottom: 1px solid #eaecef;
  margin-top: 1.5em;
  margin-bottom: .8em;
  padding-bottom: .3em;
  font-weight: 600;
  line-height: 1.25;
}

#response-output h1 {
  font-size: 2em;
}

#response-output h2 {
  font-size: 1.7em;
}

#response-output h3 {
  font-size: 1.4em;
}

#response-output p {
  margin-top: 0;
  margin-bottom: 16px;
  line-height: 1.6;
}

#response-output ul, #response-output ol {
  margin-top: 0;
  margin-bottom: 16px;
  padding-left: 2em;
}

#response-output li, #response-output li > p {
  margin-bottom: .25em;
}

#response-output pre {
  background-color: #f6f8fa;
  border-radius: 6px;
  margin-bottom: 16px;
  padding: 16px;
  line-height: 1.45;
  overflow: auto;
}

#response-output code {
  background-color: #1b1f230d;
  border-radius: 3px;
  margin: 0;
  padding: .2em .4em;
  font-family: SFMono-Regular, Consolas, Liberation Mono, Menlo, Courier, monospace;
  font-size: 85%;
}

#response-output pre > code {
  background-color: #0000;
  border: 0;
  margin: 0;
  padding: 0;
  font-size: 100%;
}

#response-output strong {
  font-weight: 600;
}

#response-output em {
  font-style: italic;
}

#response-output blockquote {
  color: #6a737d;
  border-left: .25em solid #dfe2e5;
  margin: 0 0 16px;
  padding: 0 1em;
}

#response-output hr {
  background-color: #e1e4e8;
  border: 0;
  height: .25em;
  margin: 24px 0;
  padding: 0;
}

#response-output table {
  border-collapse: collapse;
  width: 100%;
  margin-bottom: 16px;
  display: block;
  overflow: auto;
}

#response-output th, #response-output td {
  border: 1px solid #dfe2e5;
  padding: 6px 13px;
}

#response-output tr {
  background-color: #fff;
  border-top: 1px solid #c6cbd1;
}

#response-output tr:nth-child(2n) {
  background-color: #f6f8fa;
}

.modal {
  display: none;
}

.modal.is-open {
  display: block;
}

.modal__overlay {
  z-index: 1000;
  background: #0009;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  inset: 0;
}

.modal__container {
  background-color: #fff;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  max-height: 90vh;
  padding: 20px;
  overflow-y: auto;
  box-shadow: 0 5px 15px #0000004d;
}

.modal-tabs {
  border-bottom: 1px solid #e0e0e0;
  margin-bottom: 15px;
  display: flex;
}

.tab-item {
  cursor: pointer;
  color: #333;
  background: none;
  border: none;
  border-bottom: 3px solid #0000;
  margin-bottom: -1px;
  padding: 10px 15px;
  font-size: 1em;
}

.tab-item:hover {
  background-color: #f5f5f5;
}

.tab-item.active-tab {
  color: #007bff;
  border-bottom-color: #007bff;
  font-weight: bold;
}

.tab-content {
  padding: 15px 0;
  display: none;
}

.tab-content.active-tab-content {
  display: block;
}

.modal__footer {
  text-align: right;
  border-top: 1px solid #e0e0e0;
  margin-top: 20px;
  padding-top: 15px;
}

.modal__btn {
  color: #333;
  cursor: pointer;
  background-color: #f0f0f0;
  border: 1px solid #0000;
  border-radius: 6px;
  outline: none;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: .95em;
  font-weight: 500;
  line-height: 1.2;
  text-decoration: none;
  transition: background-color .2s, border-color .2s, box-shadow .2s;
}

.modal__btn:hover {
  background-color: #e0e0e0;
  border-color: #d0d0d0;
}

.modal__btn:focus-visible {
  outline-offset: 2px;
  outline: 2px solid #007bff;
}

.modal__btn-primary {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}

.modal__btn-primary:hover {
  background-color: #0056b3;
  border-color: #0056b3;
}

.modal__btn-primary:focus-visible {
  outline-color: #0056b3;
}

.modal__btn:disabled, .modal__btn-primary:disabled {
  color: #adb5bd;
  cursor: not-allowed;
  background-color: #e9ecef;
  border-color: #ced4da;
}

.required-asterisk {
  color: red;
  margin-left: 4px;
}

.chat-message {
  max-width: 85%;
  margin-bottom: 10px;
  display: flex;
}

.user-message {
  justify-content: flex-end;
  margin-left: auto;
}

.model-message {
  justify-content: flex-start;
  margin-right: auto;
}

.message-bubble {
  word-wrap: break-word;
  border-radius: 18px;
  padding: 10px 15px;
  line-height: 1.5;
}

.user-message .message-bubble {
  color: #333;
  background-color: #dcf8c6;
  border-top-right-radius: 5px;
}

.model-message .message-bubble {
  color: #333;
  background-color: #fff;
  border-top-left-radius: 5px;
}

.model-message.error-rendering .message-bubble {
  color: #c62828;
  background-color: #ffebee;
  border: 1px solid #ef9a9a;
}

.model-message .message-bubble h1, .model-message .message-bubble h2, .model-message .message-bubble p {
  margin: .5em 0;
}

#history-list li a {
  color: #0366d6;
  border-bottom: 1px solid #eee;
  padding: 8px 5px;
  font-size: .9em;
  text-decoration: none;
  display: block;
}

#history-list li a:hover {
  background-color: #f5f5f5;
}

.history-title {
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 3px;
  font-weight: 500;
  display: block;
  overflow: hidden;
}

.history-timestamp {
  color: #586069;
  font-size: .8em;
  display: block;
}
